@import '../variables';

.pizza-block {
  width: 280px;
  text-align: center;
  margin-bottom: 25px;
  border: 2px solid $orange;
  padding: 10px;
  border-radius: 20px;

  &:not(:nth-of-type(4n)) {
    margin-right: 35px;
  }

  &__image {
    width: 260px;
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 20px;
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        // @include noselect();
        &.active {
          background-color: darken(#f9f9f9, 60%);
          color: #fff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 10px;
          cursor: pointer;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }
}

.pizza__wrapper {
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  gap: 5px;
}